import React from 'react';
/**
 * WordPress Dependencies
 */
import { isEmpty } from 'lodash';
import { __ } from '@wordpress/i18n';
import { useBlockEditContext } from '@wordpress/block-editor';
import {
	__experimentalToolsPanelItem as ToolsPanelItem,
	__experimentalBorderBoxControl as BorderBoxControl,
	__experimentalBoxControl as BoxControl,
} from '@wordpress/components';

function BorderControl( props ) {
	const { clientId } = useBlockEditContext();
	return (
		<>
			<ToolsPanelItem
				resetAllFilter={ props.resetAllFilter }
				isShownByDefault={ false }
				panelId={ clientId }
				hasValue={ () => ! isEmpty( props.value.accordionBorder ) }
				label={ __( 'Accordion Border', 'ghub-accordion' ) }
				onDeselect={ () => {
					props.onValueReset( 'accordionBorder' );
				} }
			>
				<BorderBoxControl
					enableAlpha
					colors={ props.colors }
					label={ __( 'Accordion Border', 'ghub-accordion' ) }
					onChange={ ( newBorder ) => {
						props.onChange( newBorder, 'accordionBorder' );
					} }
					value={ props.value.accordionBorder }
				/>
			</ToolsPanelItem>
			<ToolsPanelItem
				resetAllFilter={ props.resetAllFilter }
				isShownByDefault={ false }
				panelId={ clientId }
				hasValue={ () =>
					! isEmpty( props.value.accordionBorderRadius )
				}
				label={ __( 'Accordion Border Radius', 'ghub-accordion' ) }
				onDeselect={ () => {
					props.onValueReset( 'accordionBorderRadius' );
				} }
			>
				<BoxControl
					resetValues={ {} }
					label={ __( 'Accordion Border Radius', 'ghub-accordion' ) }
					values={ props.value.accordionBorderRadius }
					onChange={ ( newBorderRadius ) => {
						props.onChange(
							newBorderRadius,
							'accordionBorderRadius'
						);
					} }
				/>
			</ToolsPanelItem>
			<ToolsPanelItem
				resetAllFilter={ props.resetAllFilter }
				isShownByDefault={ false }
				panelId={ clientId }
				hasValue={ () => ! isEmpty( props.value.titleBorder ) }
				label={ __( 'Title Border', 'ghub-accordion' ) }
				onDeselect={ () => {
					props.onValueReset( 'titleBorder' );
				} }
			>
				<BorderBoxControl
					enableAlpha
					colors={ props.colors }
					label={ __( 'Title Border', 'ghub-accordion' ) }
					onChange={ ( newBorder ) => {
						props.onChange( newBorder, 'titleBorder' );
					} }
					value={ props.value.titleBorder }
				/>
			</ToolsPanelItem>
			<ToolsPanelItem
				resetAllFilter={ props.resetAllFilter }
				isShownByDefault={ false }
				panelId={ clientId }
				hasValue={ () => ! isEmpty( props.value.titleBorderRadius ) }
				label={ __( 'Title Border Radius', 'ghub-accordion' ) }
				onDeselect={ () => {
					props.onValueReset( 'titleBorderRadius' );
				} }
			>
				<BoxControl
					resetValues={ {} }
					label={ __( 'Title Border Radius', 'ghub-accordion' ) }
					values={ props.value.titleBorderRadius }
					onChange={ ( newBorderRadius ) => {
						props.onChange( newBorderRadius, 'titleBorderRadius' );
					} }
				/>
			</ToolsPanelItem>
			<ToolsPanelItem
				resetAllFilter={ props.resetAllFilter }
				isShownByDefault={ false }
				panelId={ clientId }
				hasValue={ () => ! isEmpty( props.value.contentBorder ) }
				label={ __( 'Content Border', 'ghub-accordion' ) }
				onDeselect={ () => {
					props.onValueReset( 'contentBorder' );
				} }
			>
				<BorderBoxControl
					enableAlpha
					colors={ props.colors }
					label={ __( 'Content Border', 'ghub-accordion' ) }
					onChange={ ( newBorder ) => {
						props.onChange( newBorder, 'contentBorder' );
					} }
					value={ props.value.contentBorder }
				/>
			</ToolsPanelItem>
			<ToolsPanelItem
				resetAllFilter={ props.resetAllFilter }
				isShownByDefault={ false }
				panelId={ clientId }
				hasValue={ () => ! isEmpty( props.value.contentBorderRadius ) }
				label={ __( 'Content Border Radius', 'ghub-accordion' ) }
				onDeselect={ () => {
					props.onValueReset( 'contentBorderRadius' );
				} }
			>
				<BoxControl
					resetValues={ {} }
					label={ __( 'Content Border Radius', 'ghub-accordion' ) }
					values={ props.value.contentBorderRadius }
					onChange={ ( newBorderRadius ) => {
						props.onChange(
							newBorderRadius,
							'contentBorderRadius'
						);
					} }
				/>
			</ToolsPanelItem>
		</>
	);
}

export default BorderControl;
