import classNames from 'classnames';
import { getDatasets } from './get-datasets';

import { pick, omit } from 'lodash';
import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';

function save( props ) {
	const blockProps = useBlockProps.save( {
		className: classNames(
			'ghub-accordion-content ghub-accordion-content-initialized'
		),
		...getDatasets( props.attributes.enableFAQSchema ),
	} );
	const textAttributes = props.attributes.enableFAQSchema
		? { itemprop: 'text' }
		: {};
	const styles = pick( blockProps, 'style' )?.style;
	return (
		<div { ...omit( blockProps, 'style' ) }>
			<div
				style={ styles }
				{ ...textAttributes }
				className="ghub-accordion-content-inner-accordion"
			>
				<InnerBlocks.Content />
			</div>
		</div>
	);
}

export default save;
