/**
 * WordPress Dependencies
 */
import React from 'react';

import { __ } from '@wordpress/i18n';
import { useSelect } from '@wordpress/data';
import { useEffect } from '@wordpress/element';
import { InspectorControls } from '@wordpress/block-editor';
import {
	PanelBody,
	ToggleControl,
	__experimentalUnitControl as UnitControl,
} from '@wordpress/components';

/**
 * Custom Dependencies
 */
import {
	BorderControl,
	ColorSettings,
	CustomToggleGroupControl,
	DimensionControl,
} from '../../components';
import { getBlock, updateAttributes } from '../../utils';
import { AVAILABLE_ICONS, AVAILABLE_ICONS_POSITIONS } from './constant';

function Inspector( props ) {
	const { setAttributes, attributes, clientId } = props;

	const { defaultColors } = useSelect( ( select ) => {
		return {
			defaultColors: select( 'core/block-editor' )?.getSettings()
				?.__experimentalFeatures?.color?.palette?.default,
		};
	} );
	useEffect( () => {
		if ( attributes.isOpenFirst ) {
			const block = getBlock( clientId );
			block.innerBlocks.forEach( ( block, index ) => {
				if ( index !== 0 ) {
					updateAttributes( block.clientId, {
						isOpen: false,
					} );
				} else {
					updateAttributes( block.clientId, {
						isOpen: true,
					} );
				}
			} );
		}
	}, [ attributes.isOpenFirst ] );

	return (
		<>
			<InspectorControls>
				<PanelBody>
					<ToggleControl
						label={ __( 'Enable FAQ Schema', 'ghub-accordion' ) }
						checked={ attributes.enableFAQSchema }
						onChange={ () =>
							setAttributes( {
								enableFAQSchema: ! attributes.enableFAQSchema,
							} )
						}
					/>
					<ToggleControl
						label={ __( 'Open one at a time', 'ghub-accordion' ) }
						checked={ attributes.isOpenOneAtATime }
						onChange={ () =>
							setAttributes( {
								isOpenOneAtATime: ! attributes.isOpenOneAtATime,
							} )
						}
					/>
					<ToggleControl
						label={ __( 'Open First', 'ghub-accordion' ) }
						checked={ attributes.isOpenFirst }
						onChange={ () =>
							setAttributes( {
								isOpenFirst: ! attributes.isOpenFirst,
							} )
						}
					/>
					<ToggleControl
						label={ __( 'Show Icon', 'ghub-accordion' ) }
						checked={ attributes.isShowIcon }
						onChange={ () =>
							setAttributes( {
								isShowIcon: ! attributes.isShowIcon,
							} )
						}
					/>
					{ attributes.isShowIcon && (
						<>
							<CustomToggleGroupControl
								attributeKey="iconPosition"
								options={ AVAILABLE_ICONS_POSITIONS }
								label={ __(
									'Icon Position',
									'ghub-accordion'
								) }
							/>
							<CustomToggleGroupControl
								attributeKey="accordionIcon"
								options={ AVAILABLE_ICONS }
								label={ __( 'Icon', 'ghub-accordion' ) }
							/>
							{ attributes.iconPosition === 'left' && (
								<UnitControl
									label="Icon Spacing"
									value={ attributes.iconSpacing }
									onChange={ ( newSpacing ) =>
										setAttributes( {
											iconSpacing: newSpacing,
										} )
									}
									min={ 0 }
								/>
							) }
						</>
					) }
				</PanelBody>
			</InspectorControls>
			<>
				<InspectorControls group="color">
					<ColorSettings labelPrefix="Item" />
				</InspectorControls>
				<InspectorControls group="dimensions">
					<DimensionControl
						inputProps={ { min: -Infinity } }
						value={ {
							accordionPadding: attributes?.accordionPadding,
							accordionGap: attributes?.accordionGap,
							titlePadding: attributes?.titlePadding,
							contentPadding: attributes?.contentPadding,
						} }
						onChange={ ( value, type ) => {
							if ( type === 'accordionPadding' ) {
								props.setAttributes( {
									accordionPadding: value,
								} );
							} else if ( type === 'accordionGap' ) {
								props.setAttributes( { accordionGap: value } );
							} else if ( type === 'titlePadding' ) {
								props.setAttributes( { titlePadding: value } );
							} else if ( type === 'contentPadding' ) {
								props.setAttributes( {
									contentPadding: value,
								} );
							}
						} }
						onReset={ ( type ) => {
							if ( type === 'accordionPadding' ) {
								props.setAttributes( {
									accordionPadding: {},
								} );
							} else if ( type === 'accordionGap' ) {
								props.setAttributes( { accordionGap: {} } );
							} else if ( type === 'titlePadding' ) {
								props.setAttributes( { titlePadding: {} } );
							} else if ( type === 'contentPadding' ) {
								props.setAttributes( {
									contentPadding: {},
								} );
							}
						} }
					/>
				</InspectorControls>
				<InspectorControls group="border">
					<BorderControl
						colors={ defaultColors }
						value={ {
							accordionBorder: attributes?.accordionBorder,
							accordionBorderRadius:
								attributes?.accordionBorderRadius,
							titleBorder: attributes?.titleBorder,
							titleBorderRadius: attributes?.titleBorderRadius,
							contentBorder: attributes?.contentBorder,
							contentBorderRadius:
								attributes?.contentBorderRadius,
						} }
						resetAllFilter={ () => {
							setAttributes( {
								accordionBorder: {},
								accordionBorderRadius: {},
								titleBorder: {},
								titleBorderRadius: {},
								contentBorder: {},
								contentBorderRadius: {},
							} );
						} }
						onChange={ ( value, attributeKey ) => {
							setAttributes( {
								[ attributeKey ]: value,
							} );
						} }
						onReset={ ( attributeKey ) => {
							setAttributes( {
								[ attributeKey ]: {},
							} );
						} }
						onValueReset={ ( attributeKey ) =>
							setAttributes( { [ attributeKey ]: {} } )
						}
					/>
					{ /* <BorderControl
						labelPrefix="Title"
						colors={ defaultColors }
						borderValue={ attributes.titleBorder }
						borderRadiusValue={ attributes.titleBorderRadius }
						onBorderChange={ ( newBorder ) =>
							setAttributes( { titleBorder: newBorder } )
						}
						onBorderReset={ () =>
							setAttributes( { titleBorder: {} } )
						}
						onBorderRadiusReset={ () =>
							setAttributes( { titleBorderRadius: {} } )
						}
						onBorderRadiusChange={ ( newBorderRadius ) =>
							setAttributes( {
								titleBorderRadius: newBorderRadius,
							} )
						}
					/>
					<BorderControl
						labelPrefix="Content"
						colors={ defaultColors }
						borderValue={ attributes.contentBorder }
						borderRadiusValue={ attributes.contentBorderRadius }
						onBorderChange={ ( newBorder ) =>
							setAttributes( { contentBorder: newBorder } )
						}
						onBorderReset={ () =>
							setAttributes( { contentBorder: {} } )
						}
						onBorderRadiusReset={ () =>
							setAttributes( { contentBorderRadius: {} } )
						}
						onBorderRadiusChange={ ( newBorderRadius ) =>
							setAttributes( {
								contentBorderRadius: newBorderRadius,
							} )
						}
					/> */ }
				</InspectorControls>
			</>
		</>
	);
}

export default Inspector;
