import classNames from 'classnames';
import { isEmpty } from 'lodash';
import {
	BlockControls,
	InnerBlocks,
	useBlockProps,
	JustifyContentControl,
} from '@wordpress/block-editor';
import { useEffect } from '@wordpress/element';
import { getDatasets } from './get-datasets';
import {
	getIcon,
	getParentBlock,
	getRootClientId,
	updateAttributes,
} from '../../utils';

function edit( props ) {
	const { clientId, attributes, setAttributes, context } = props;
	const blockProps = useBlockProps( {
		className: classNames( 'ghub-accordion-title', {
			[ `justify-content-${ attributes.justification }` ]: ! isEmpty(
				attributes.justification
			),
			[ `ghub-accordion-icon-position-${ attributes.iconPosition }` ]: ! isEmpty(
				attributes.iconPosition
			),
		} ),
		...getDatasets( attributes.enableFAQSchema ),
	} );

	const template = [
		[
			'core/heading',
			{
				level: 3,
				placeholder: 'Add title',
				content: 'Title Here',
			},
			[],
		],
	];

	const rootClientId = getRootClientId( clientId );

	const setAccordionStatus = () => {
		if ( ! context?.isOpenOneAtATime ) {
			updateAttributes( rootClientId, {
				isOpen: ! context.isOpen,
			} );
		} else {
			const parentBlock = getParentBlock( clientId, 'ghub/accordion' );

			parentBlock.innerBlocks.forEach( ( block ) => {
				if ( block.clientId !== rootClientId ) {
					updateAttributes( block.clientId, {
						isOpen: false,
					} );
				} else {
					updateAttributes( block.clientId, {
						isOpen: ! context.isOpen,
					} );
				}
			} );
		}
	};

	useEffect( () => {
		setAttributes( {
			enableFAQSchema: context.enableFAQSchema,
		} );
	}, [ context.enableFAQSchema ] );

	useEffect( () => {
		setAttributes( { accordionIcon: context.accordionIcon } );
	}, [ context.accordionIcon ] );

	useEffect( () => {
		setAttributes( { isShowIcon: context.isShowIcon } );
	}, [ context.isShowIcon ] );

	useEffect( () => {
		setAttributes( { iconPosition: context.iconPosition } );
	}, [ context.iconPosition ] );

	const currentIcon = getIcon( attributes.accordionIcon );

	return (
		<div { ...blockProps }>
			<BlockControls group="block">
				<JustifyContentControl
					value={ attributes.justification }
					onChange={ ( next ) => {
						setAttributes( { justification: next } );
					} }
				/>
			</BlockControls>
			<div
				onClick={ setAccordionStatus }
				className="ghub-accordion-title-inner-accordion"
			>
				<div className="ghub-accordion-title-icon-accordion">
					{ attributes.isShowIcon &&
						attributes.iconPosition === 'left' &&
						currentIcon }
					<InnerBlocks template={ template } />
				</div>
				{ attributes.isShowIcon &&
					attributes.iconPosition === 'right' &&
					currentIcon }
			</div>
		</div>
	);
}

export default edit;
