import { pick, omit } from 'lodash';
import { useEffect } from '@wordpress/element';
import { InnerBlocks, useBlockProps } from '@wordpress/block-editor';

import classNames from 'classnames';
import { getDatasets } from './get-datasets';

function edit( props ) {
	const blockProps = useBlockProps( {
		className: classNames( 'ghub-accordion-content' ),
		...getDatasets( props.attributes.enableFAQSchema ),
	} );
	const template = [
		[
			'core/paragraph',
			{
				placeholder: 'Write content or Type / to choose a block',
				content: 'Accordion Content',
			},
			[],
		],
	];

	useEffect( () => {
		props.setAttributes( {
			enableFAQSchema: props.context.enableFAQSchema,
		} );
	}, [ props.context.enableFAQSchema ] );

	const textAttributes = props.attributes.enableFAQSchema
		? { itemprop: 'text' }
		: {};

	const styles = pick( blockProps, 'style' )?.style;

	return (
		<>
			<div { ...omit( blockProps, 'style' ) }>
				<div
					style={ styles }
					{ ...textAttributes }
					className="ghub-accordion-content-inner-accordion"
				>
					<InnerBlocks template={ template } />
				</div>
			</div>
		</>
	);
}

export default edit;
