/**
 * WordPress Dependencies
 */

import { useDispatch, useSelect } from '@wordpress/data';
import { __, sprintf } from '@wordpress/i18n';
import {
	useBlockEditContext,
	__experimentalColorGradientSettingsDropdown as ColorGradientSettingsDropdown,
	__experimentalUseMultipleOriginColorsAndGradients as useMultipleOriginColorsAndGradients,
} from '@wordpress/block-editor';

/**
 * Custom Dependencies
 */
import PropTypes from 'prop-types';

function ColorSettings( props ) {
	const { labelPrefix } = props;
	const { clientId } = useBlockEditContext();
	const { updateBlockAttributes } = useDispatch( 'core/block-editor' );

	const attributes = useSelect( ( select ) => {
		return select( 'core/block-editor' ).getBlockAttributes( clientId );
	} );
	const setAttributes = ( newAttributes ) =>
		updateBlockAttributes( clientId, newAttributes );
	const colorGradientSettings = useMultipleOriginColorsAndGradients();
	const { defaultColors, defaultGradients } = useSelect( ( select ) => {
		return {
			defaultColors: select( 'core/block-editor' )?.getSettings()
				?.__experimentalFeatures?.color?.palette?.default,
			defaultGradients: select( 'core/block-editor' )?.getSettings()
				?.__experimentalFeatures?.color?.gradients?.default,
		};
	} );

	return (
		<ColorGradientSettingsDropdown
			{ ...colorGradientSettings }
			panelId={ clientId }
			title={ __( 'Color Settings', 'ghub-accordion' ) }
			popoverProps={ {
				placement: 'left start',
			} }
			settings={ [
				{
					colorValue: attributes.accordionBackground,
					gradientValue: attributes.accordionGradientBackground,
					colors: defaultColors,
					gradients: defaultGradients,
					label: sprintf(
						__( '%1$s Background', 'ghub-accordion' ),
						labelPrefix
					),
					onColorChange: ( newValue ) =>
						setAttributes( { accordionBackground: newValue } ),
					onGradientChange: ( newValue ) =>
						setAttributes( {
							accordionGradientBackground: newValue,
						} ),
				},
				{
					colorValue: attributes.activeAccordionBackground,
					gradientValue: attributes.activeAccordionGradientBackground,
					colors: defaultColors,
					gradients: defaultGradients,
					label: sprintf(
						__( 'Active %1$s Background', 'ghub-accordion' ),
						labelPrefix
					),
					onColorChange: ( newValue ) => {
						setAttributes( {
							activeAccordionBackground: newValue,
						} );
					},
					onGradientChange: ( newValue ) => {
						setAttributes( {
							activeAccordionGradientBackground: newValue,
						} );
					},
				},
				{
					colorValue: attributes.titleColor,
					colors: defaultColors,
					label: __( 'Title Color', 'ghub-accordion' ),
					onColorChange: ( newValue ) =>
						setAttributes( { titleColor: newValue } ),
				},

				{
					colorValue: attributes.activeTitleColor,
					colors: defaultColors,
					label: __( 'Active Title Color', 'ghub-accordion' ),
					onColorChange: ( newValue ) =>
						setAttributes( { activeTitleColor: newValue } ),
				},
				{
					colorValue: attributes.titleBackground,
					gradientValue: attributes.titleGradientBackground,
					label: __( 'Title Background', 'ghub-accordion' ),
					colors: defaultColors,
					gradients: defaultGradients,
					onColorChange: ( newValue ) =>
						setAttributes( {
							titleBackground: newValue,
						} ),
					onGradientChange: ( newValue ) =>
						setAttributes( {
							titleGradientBackground: newValue,
						} ),
				},
				{
					colorValue: attributes.titleActiveBackground,
					gradientValue: attributes.titleActiveGradientBackground,
					colors: defaultColors,
					label: __( 'Active Title Background', 'ghub-accordion' ),
					onColorChange: ( newValue ) =>
						setAttributes( {
							titleActiveBackground: newValue,
						} ),
					gradients: defaultGradients,
					onGradientChange: ( newValue ) => {
						setAttributes( {
							titleActiveGradientBackground: newValue,
						} );
					},
				},
				{
					colorValue: attributes.contentColor,
					colors: defaultColors,
					label: __( 'Content Color', 'gutenberghub-query-sort' ),
					onColorChange: ( newValue ) =>
						setAttributes( { contentColor: newValue } ),
				},
				{
					colorValue: attributes.contentBackground,
					gradientValue: attributes.contentGradientBackground,
					label: __( 'Content Background', 'ghub-accordion' ),
					colors: defaultColors,
					gradients: defaultGradients,
					onColorChange: ( newValue ) =>
						setAttributes( {
							contentBackground: newValue,
						} ),
					onGradientChange: ( newValue ) =>
						setAttributes( {
							contentGradientBackground: newValue,
						} ),
				},
			] }
		/>
	);
}

ColorSettings.propTypes = {
	/**Add add prefix on label*/
	labelPrefix: PropTypes.string.isRequired,
};
export default ColorSettings;
