import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';

import classNames from 'classnames';
import { getStyles } from './get-styles';
import { getDatasets } from './get-datasets';

function save( props ) {
	const blockProps = useBlockProps.save( {
		className: classNames( 'ghub-accordion' ),
		'data-open_one': props.attributes.isOpenOneAtATime,
		'data-open_first': props.attributes.isOpenFirst,
		style: getStyles( props.attributes ),
		...getDatasets( props.attributes.enableFAQSchema ),
	} );

	return (
		! props.attributes.showPlaceholder && (
			<div { ...blockProps }>
				<InnerBlocks.Content />
			</div>
		)
	);
}

export default save;
