/**
 * WordPress Dependencies
 */
import {
	__experimentalToggleGroupControl as ToggleGroupControl,
	__experimentalToggleGroupControlOptionIcon as ToggleGroupControlOptionIcon,
} from '@wordpress/components';

import { useDispatch, useSelect } from '@wordpress/data';
import { useBlockEditContext } from '@wordpress/block-editor';
/**
 * Custom Dependencies
 */
import PropTypes from 'prop-types';

/**
 *
 * @param {String} value - Group control selected value
 * @param {String} label - Group control label
 * @param {Array} options - Group control available options
 * @param {Function} onChange - Function fires when value is changed
 */
function CustomToggleGroupControl( props ) {
	const { attributeKey, label, options } = props;

	const { clientId } = useBlockEditContext();
	const { updateBlockAttributes } = useDispatch( 'core/block-editor' );

	const attributes = useSelect( ( select ) => {
		return select( 'core/block-editor' ).getBlockAttributes( clientId );
	} );
	const setAttributes = ( newAttributes ) =>
		updateBlockAttributes( clientId, newAttributes );

	return (
		<ToggleGroupControl
			__nextHasNoMarginBottom
			className="ghub-accordion-icon-control"
			label={ label }
			value={ attributes[ attributeKey ] }
			onChange={ ( newValue ) => {
				setAttributes( {
					[ attributeKey ]: newValue,
				} );
			} }
		>
			{ options.map( ( { value, icon, label } ) => {
				return (
					<ToggleGroupControlOptionIcon
						key={ value }
						value={ value }
						icon={ icon }
						label={ label }
					/>
				);
			} ) }
		</ToggleGroupControl>
	);
}

CustomToggleGroupControl.propTypes = {
	/**Attribute key to update attribute */
	attributeKey: PropTypes.string.isRequired,

	/**Display as a component label */
	label: PropTypes.string,

	/**Available Options for the component */
	options: PropTypes.array.isRequired,
};
export default CustomToggleGroupControl;
