import { getBorderCSS, getBorderVariablesCss } from '../../utils';
import { isEmpty, omitBy, isUndefined, trim } from 'lodash';

export function getStyles( attributes ) {
	const accordionPadding = attributes?.accordionPadding;
	const accordionGap = attributes?.accordionGap;
	const borderRadius = attributes?.accordionBorderRadius;
	const titleRadius = attributes?.titleBorderRadius;
	const contentRadius = attributes?.contentBorderRadius;
	const border = getBorderCSS( attributes?.accordionBorder );
	const titleBorder = getBorderCSS( attributes?.titleBorder );
	const contentBorder = getBorderCSS( attributes?.contentBorder );

	const accordionItemBorder = getBorderVariablesCss(
		border,
		'accordion-item'
	);
	const accordionTitleBorder = getBorderVariablesCss(
		titleBorder,
		'accordion-item-title'
	);
	const accordionContentBorder = getBorderVariablesCss(
		contentBorder,
		'accordion-item-content'
	);

	let styles = {
		'--ghub-accordion-icon-spacing': attributes?.iconSpacing,
		'--ghub-accordion-item-title-color': attributes?.titleColor,
		'--ghub-accordion-item-title-bg-color': attributes?.titleBackground,
		'--ghub-accordion-item-title-bg-active-color':
			attributes?.titleActiveBackground,
		'--ghub-accordion-item-content-color': attributes?.contentColor,
		'--ghub-accordion-item-content-bg-color': attributes?.contentBackground,
		'--ghub-accordion-item-bg-color': attributes?.accordionBackground,
		'--ghub-accordion-item-gradient-color':
			attributes?.accordionGradientBackground,
		'--ghub-accordion-item-title-gradient-color':
			attributes?.titleGradientBackground,
		'--ghub-accordion-item-content-gradient-color':
			attributes?.contentGradientBackground,
		'--ghub-accordion-item-active-title-color':
			attributes?.activeTitleColor,
		'--ghub-accordion-item-active-bg-color':
			attributes?.activeAccordionBackground,
		'--ghub-accordion-item-active-gradient-color':
			attributes?.activeAccordionGradientBackground,
		'--ghub-accordion-item-title-active-gradient-color':
			attributes?.titleActiveGradientBackground,
		'--ghub-accordion-item-padding-top': accordionPadding?.top,
		'--ghub-accordion-item-padding-right': accordionPadding?.right,
		'--ghub-accordion-item-padding-bottom': accordionPadding?.bottom,
		'--ghub-accordion-item-padding-left': accordionPadding?.left,

		'--ghub-accordion-item-title-padding-top':
			attributes?.titlePadding?.top,
		'--ghub-accordion-item-title-padding-right':
			attributes?.titlePadding?.right,
		'--ghub-accordion-item-title-padding-bottom':
			attributes?.titlePadding?.bottom,
		'--ghub-accordion-item-title-padding-left':
			attributes?.titlePadding?.left,

		'--ghub-accordion-item-content-padding-top':
			attributes?.contentPadding?.top,
		'--ghub-accordion-item-content-padding-right':
			attributes?.contentPadding?.right,
		'--ghub-accordion-item-content-padding-bottom':
			attributes?.contentPadding?.bottom,
		'--ghub-accordion-item-content-padding-left':
			attributes?.contentPadding?.left,

		'--ghub-accordion-item-gap': accordionGap,
		'--ghub-accordion-item-top-left-radius': borderRadius?.top,
		'--ghub-accordion-item-top-right-radius': borderRadius?.right,
		'--ghub-accordion-item-bottom-left-radius': borderRadius?.bottom,
		'--ghub-accordion-item-bottom-right-radius': borderRadius?.left,
		'--ghub-accordion-item-title-top-left-radius': titleRadius?.top,
		'--ghub-accordion-item-title-top-right-radius': titleRadius?.right,
		'--ghub-accordion-item-title-bottom-left-radius': titleRadius?.bottom,
		'--ghub-accordion-item-title-bottom-right-radius': titleRadius?.left,
		'--ghub-accordion-item-content-top-left-radius': contentRadius?.top,
		'--ghub-accordion-item-content-top-right-radius': contentRadius?.right,
		'--ghub-accordion-item-content-bottom-left-radius':
			contentRadius?.bottom,
		'--ghub-accordion-item-content-bottom-right-radius':
			contentRadius?.left,
		...accordionItemBorder,
		...accordionTitleBorder,
		...accordionContentBorder,
	};

	return omitBy(
		styles,
		( value ) =>
			isUndefined( value ) ||
			value === false ||
			trim( value ) === '' ||
			trim( value ) === 'undefined undefined undefined' ||
			isEmpty( value )
	);
}
