/**
 * WordPress Dependencies
 */
import { isEmpty } from 'lodash';
import { __ } from '@wordpress/i18n';
import { useBlockEditContext } from '@wordpress/block-editor';
import {
	__experimentalToolsPanelItem as ToolsPanelItem,
	__experimentalBoxControl as WPBoxControl,
	__experimentalUnitControl as UnitControl,
} from '@wordpress/components';

/**
 * Custom Dependencies
 */

function DimensionControl( props ) {
	const { clientId } = useBlockEditContext();

	return (
		<>
			<ToolsPanelItem
				isShownByDefault={ false }
				panelId={ clientId }
				hasValue={ () => ! isEmpty( props.value.accordionPadding ) }
				label="Accordion Padding"
				onDeselect={ () => props.onReset( 'accordionPadding' ) }
			>
				<WPBoxControl
					{ ...props }
					label="Accordion Padding"
					resetValues={ {} }
					values={ props.value.accordionPadding }
					onChange={ ( newValue ) =>
						props.onChange( newValue, 'accordionPadding' )
					}
					inputProps={ { min: 0 } }
				/>
			</ToolsPanelItem>
			<ToolsPanelItem
				isShownByDefault={ false }
				panelId={ clientId }
				hasValue={ () => ! isEmpty( props.value.titlePadding ) }
				label="Title Padding"
				onDeselect={ () => props.onReset( 'titlePadding' ) }
			>
				<WPBoxControl
					{ ...props }
					label="Title Padding"
					resetValues={ {} }
					values={ props.value.titlePadding }
					onChange={ ( newValue ) =>
						props.onChange( newValue, 'titlePadding' )
					}
					inputProps={ { min: 0 } }
				/>
			</ToolsPanelItem>
			<ToolsPanelItem
				isShownByDefault={ false }
				panelId={ clientId }
				hasValue={ () => ! isEmpty( props.value.contentPadding ) }
				label="Content Padding"
				onDeselect={ () => props.onReset( 'contentPadding' ) }
			>
				<WPBoxControl
					{ ...props }
					label="Content Padding"
					resetValues={ {} }
					values={ props.value.contentPadding }
					onChange={ ( newValue ) =>
						props.onChange( newValue, 'contentPadding' )
					}
					inputProps={ { min: 0 } }
				/>
			</ToolsPanelItem>
			<ToolsPanelItem
				isShownByDefault={ false }
				panelId={ clientId }
				hasValue={ () => ! isEmpty( props.value.accordionGap ) }
				label="Accordion Gap"
				onDeselect={ () => props.onReset( 'accordionGap' ) }
			>
				<UnitControl
					{ ...props }
					label="Accordion Gap"
					value={ props.value.accordionGap }
					onChange={ ( newValue ) =>
						props.onChange( newValue, 'accordionGap' )
					}
				/>
			</ToolsPanelItem>
		</>
	);
}

export default DimensionControl;
