import classNames from 'classnames';
import { isEmpty } from 'lodash';
import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';
import { getIcon } from '../../utils';
import { getDatasets } from './get-datasets';

function save( props ) {
	const { attributes } = props;
	const blockProps = useBlockProps.save( {
		className: classNames( 'ghub-accordion-title', {
			[ `justify-content-${ attributes.justification }` ]: ! isEmpty(
				attributes.justification
			),
			[ `ghub-accordion-icon-position-${ attributes.iconPosition }` ]: ! isEmpty(
				attributes.iconPosition
			),
		} ),
		...getDatasets( props.attributes.enableFAQSchema ),
	} );
	const currentIcon = getIcon( props.attributes.accordionIcon );

	return (
		<div { ...blockProps }>
			<div className="ghub-accordion-title-inner-accordion">
				<div className="ghub-accordion-title-icon-accordion">
					{ props.attributes.isShowIcon &&
						props.attributes.iconPosition === 'left' &&
						currentIcon }
					<InnerBlocks.Content />
				</div>
				{ props.attributes.isShowIcon &&
					props.attributes.iconPosition === 'right' &&
					currentIcon }
			</div>
		</div>
	);
}

export default save;
