/**
 * Wordpress Dependencies
 */
import { useEffect } from '@wordpress/element';
import { __ } from '@wordpress/i18n';
import { useBlockProps, InnerBlocks } from '@wordpress/block-editor';
import { getDatasets } from './get-datasets';
/**
 * Custom Imports
 */
import classNames from 'classnames';

function edit( props ) {
	const { attributes } = props;
	const blockProps = useBlockProps( {
		className: classNames( 'ghub-accordion-item', {
			'ghub-active-accordion': attributes.isOpen,
		} ),
		...getDatasets( props.attributes.enableFAQSchema ),
	} );

	useEffect( () => {
		props.setAttributes( {
			enableFAQSchema: props.context.enableFAQSchema,
		} );
	}, [ props.context.enableFAQSchema ] );

	const template = [
		[ 'ghub/accordion-title', {}, [] ],
		[ 'ghub/accordion-content', {}, [] ],
	];

	return (
		<>
			<div { ...blockProps }>
				<InnerBlocks template={ template } />
			</div>
		</>
	);
}

export default edit;
