import { __ } from '@wordpress/i18n';
import {
	InnerBlocks,
	useBlockProps,
	BlockControls,
} from '@wordpress/block-editor';
import {
	ToolbarGroup,
	ToolbarButton,
	Placeholder,
	Button,
} from '@wordpress/components';
import { useDispatch } from '@wordpress/data';
import { isEmpty } from 'lodash';

import classNames from 'classnames';
import Inspector from './inspector';
import { getStyles } from './get-styles';
import insertAccordionBlock from '../../utils';
import templates from './templates';
import { getDatasets } from './get-datasets';
import { accordion } from '../../icons';

function edit( props ) {
	const alignment = ! isEmpty( props?.attributes?.align )
		? ' align' + props.attributes.align
		: '';
	const blockProps = useBlockProps( {
		className: classNames( `ghub-accordion${ alignment }` ),
		style: getStyles( props.attributes ),
		...getDatasets( props.attributes.enableFAQSchema ),
	} );

	const template = [ [ 'ghub/accordion-item', {}, [] ] ];

	const { replaceBlock } = useDispatch( 'core/block-editor' );

	const selectTemplate = ( blockTemplate ) => {
		props.setAttributes( props.clientId, {
			showPlaceholder: false,
		} );

		let createdBlock = wp.blocks.createBlocksFromInnerBlocksTemplate(
			blockTemplate
		);

		replaceBlock( props.clientId, createdBlock );
	};

	return (
		<>
			{ ! props.attributes.showPlaceholder && (
				<>
					<BlockControls>
						<ToolbarGroup>
							<ToolbarButton
								onClick={ () =>
									insertAccordionBlock(
										template,
										props.clientId
									)
								}
							>
								{ __( 'Add Accordion', 'ghub-accordion' ) }
							</ToolbarButton>
						</ToolbarGroup>
					</BlockControls>
					<div { ...blockProps }>
						<InnerBlocks template={ template } />
					</div>
					<Inspector { ...props } />
				</>
			) }
			{ props.attributes.showPlaceholder && (
				<div { ...blockProps }>
					<Placeholder
						icon={ accordion }
						className="ghub-accordion-placeholder"
						label={ __( 'Accordion', 'ghub-accordion' ) }
						instructions={ __(
							'Select a accordion pattern to start with',
							'ghub-accordion'
						) }
					>
						<div className="ghub-accordion-patterns">
							{ templates.map( ( template, index ) => {
								return (
									<Button
										key={ index }
										onClick={ () =>
											selectTemplate( template.template )
										}
										icon={ template.icon }
										label={ template.label }
									/>
								);
							} ) }
						</div>
					</Placeholder>
				</div>
			) }
		</>
	);
}

export default edit;
