/**
 * WordPress dependencies
 */
import { isEmpty } from 'lodash';
import { select, dispatch } from '@wordpress/data';
import { __experimentalHasSplitBorders as hasSplitBorders } from '@wordpress/components';
import { arrow, operator } from './icons';

export function getSingleSideBorderValue( border, side ) {
	return `${ border[ side ]?.width ?? '' } ${
		border[ side ]?.width && isEmpty( border[ side ]?.style )
			? 'solid'
			: border[ side ]?.style ?? ''
	} ${ border[ side ]?.color ?? '' }`;
}

export function getBorderVariablesCss( border, slug ) {
	const borderSides = [ 'top', 'right', 'bottom', 'left' ];
	let borders = {};
	for ( let i = 0; i < borderSides.length; i++ ) {
		const side = borderSides[ i ];
		const sideProperty = [ `--ghub-${ slug }-border-${ side }` ];
		const sideValue = getSingleSideBorderValue( border, side );
		borders[ sideProperty ] = sideValue;
	}

	return borders;
}

/**
 * Function that helps you to generate top right bottom left css.
 *
 * @param {object} object object has top right bottom left css
 *
 * @return {{ css:object }} A css object
 */
export const getDimensionCSS = ( object ) => {
	let css = {};

	for ( const [ key, value ] of Object.entries( object ) ) {
		css[ key ] = value;
	}
	return css;
};

/**
 * Function that's help you to generate splitted or non splitted border CSS.
 * @param {object} object border attributes
 *
 * @return {{ css:object }} A css object
 */
export const getBorderCSS = ( object ) => {
	let css = {};

	if ( ! hasSplitBorders( object ) ) {
		css[ 'top' ] = object;
		css[ 'right' ] = object;
		css[ 'bottom' ] = object;
		css[ 'left' ] = object;
		return css;
	}
	return object;
};

/**
 * Get Block Hierarchy Root ClientId
 */
export function getHierarchyRootClientId( clientId ) {
	return select( 'core/block-editor' ).getBlockHierarchyRootClientId(
		clientId
	);
}

/**
 * Get Block Root ClientId
 */
export function getRootClientId( clientId ) {
	return select( 'core/block-editor' ).getBlockRootClientId( clientId );
}
/**
 * Get Block
 */
export function getBlock( clientId ) {
	return select( 'core/block-editor' ).getBlock( clientId );
}

/**
 * Update block attributes
 */
export function updateAttributes( clientId, attributes ) {
	const { updateBlockAttributes } = dispatch( 'core/block-editor' );

	updateBlockAttributes( clientId, attributes );
}
/**
 * Insert Accordion block
 *
 * @param {array} template
 * @param {string} clientId
 */
export default function insertAccordionBlock( template, clientId ) {
	if ( ! Array.isArray( template ) || isEmpty( template ) ) {
		template = [];
	}
	const { insertBlocks } = dispatch( 'core/block-editor' );

	const accordionContainer = getBlock( clientId );

	const insertionIndex = accordionContainer.innerBlocks.length + 1;

	const accordionBlock = wp.blocks.createBlocksFromInnerBlocksTemplate(
		template
	);

	insertBlocks( accordionBlock, insertionIndex, clientId, true );
}
/**
 *
 * @param {string} iconName - name of the icon whether it is in the wordpress library or custom library
 * @returns {HTMLElement}
 */
export function getIcon( iconName ) {
	switch ( iconName ) {
		case 'operator':
			return operator;
		case 'arrow':
			return arrow;
		default:
			arrow;
			break;
	}
}

export function getParentBlock( clientId, slug ) {
	const parentsClientId = select( 'core/block-editor' )
		.getBlockParents( clientId )
		.reverse();
	let block = {};
	parentsClientId.forEach( ( blockId ) => {
		const currentBlock = getBlock( blockId );
		if ( currentBlock?.name === slug ) {
			block = currentBlock;
		}
	} );
	return block;
}
